import react from 'react';
import{AutoComplete} from 'antd'; 
import './Header.css'

const Header = () =>{
    const options =[
        {
            label:'one',
            value:'one'
        },
        {
            label:'tow',
            value:'tow'
        }
    ]


    return(
     <header>
        <div className='container'>
        <div className='thecontainer'>
            <div className='first-container'>
            <div className='main'>
                <h1>Read reviews Write reviews Find companies you can trust</h1>
            </div>
            <div>
                <AutoComplete className='input'style = {{width:550,height:60}} 
                 placeholder='company or categorie'
                 options={options} 
                 filterOption={true}
                />
            {/* <input type="text" placeholder='company or categorie'> */}
            {/* </input> */}
            <button className='second_bottom'>Search</button>
        </div>
            </div>
            </div>
        </div>
     </header>
    )
}
export default Header;